<template>
  <div class="news">

    <!--  -->
    <div class="title_bg">
      <div class="title_box">
        <p class="p_left"></p>
        <div class="title_arrow"></div>
        <p class="p_right"><span></span> / </p>
      </div>
    </div>
    <!--  -->

    <div class="list_page">
      <router-view  v-if="isRouterAlive"></router-view>

      <!---->
      <div class="hot">
        <p></p>
        <ul>
          <li v-for="(hotnews,index) in hotNewsList" :key="index">
            <router-link :to="'/newsDetail/'+hotnews.id" @click.native="refresh">{{hotnews.title}}</router-link>
            <span class="data">{{hotnews.ctime}}</span>
          </li>
        </ul>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    name: 'news',

    provide(){
      return{
        reload:this.reload
      }
    },


    data(){
      return{
        hotNewsList:[],
        isRouterAlive:true,
      }
    },
    created(){
      this.$http.get('/api/news/hot/').then(function(response){
        if(response.status==200){
          this.hotNewsList =  response.body.data;
        }
      });
    },
    methods:{
      refresh(){
       this.reload();
      },

      reload(){
        this.isRouterAlive = false;
        this.$nextTick(function () {
          this.isRouterAlive = true
        });
      },


    }
  }
</script>

<style scoped>


</style>



