<template>
  <div class="reset-psw">
    <div class="reset_box">
      <div class="lock_title">
        <span></span>
      </div>
      <div class="confirm">
        <div class="block">
          <p>1.</p>
        </div>
        <div class="arrow"></div>
      </div>
      <div class="reset">
        <div class="re_arrow"></div>
        <div class="re_block">
          <p>2.</p>
        </div>
      </div>
      <div class="ipt_box">
        <p></p>
        <input type="text" placeholder="" class="email_txt">
        <input type="button" value="" class="email_btn">
        <input type="text" placeholder="" class="verify">
        <input type="button" value=""  class="next" @click="nextBtn">
      </div>
      <div class="ret_pwd">
        <p><input type="text" placeholder="" class="email_txt"></p>
        <p><input type="password" placeholder="" class="password"></p>
        <p><input type="password" placeholder="" class="sure_password"></p>
        <button class="finish"></button>
      </div>
    </div>
  </div>
</template>

<script>
  import '../../static/js/index/js.js'
  export default {
    name: 'reset-psw',
    data(){
      return{

      }
    },
    methods:{
      nextBtn:function () {
        $('.block').css('background',"#F8F4E9");
        $('.block p').css('color',"#000");
        $('.arrow').css('border-left-color',"#F8F4E9");
        $('.re_block').css('background',"#051827");
        $('.re_block p').css('color',"#fff");
        $(".ipt_box").hide();
        $(".ret_pwd").show();
      }
    }
  }
</script>

<style scoped>
  .reset-psw{
    background: #fff;
  }

</style>



